<template>
  <div class="singer" ref="singer">
    <listview @select="selectSinger" :dataList="singers" ref="list"></listview>
    <router-view></router-view>
  </div>
</template>

<script>
  import {getSingerList} from 'api/singer'
  import {ERR_OK} from 'api/config'
  import Singer from 'common/js/singer'
  import Listview from 'base/listview/listview'
  import {mapMutations} from 'vuex'
  import {playlistMixin} from 'common/js/mixin'
  
  const HOT_NAME = ''
  const HOT_SINGER_LEN = 10
  
  export default{
    mixins: [playlistMixin],
    components: {
      Listview
    },
    data () {
      return {
        singers: []
      }
    },
    mounted() {
      this._getSingerList()
    },
    methods: {
      handlePlaylist(playlist) {
        let bottom = playlist.length > 0 ? '60px' : ''
        this.$refs.singer.style.bottom = bottom
        this.$refs.list.refresh()
      },
      selectSinger(singer) {
        //   :<router-link :to="...">
        // {}route
        // router.replacepush history 
        this.$router.push({
          path: `/singer/${singer.id}`
        })
        
        this.setSinger(singer)
      },
      _getSingerList() {
        getSingerList().then(res => {
          if(res.code === ERR_OK) {
            this.singers = this._normalizeSinger(res.data.list)
//            console.log(this._normalizeSinger(this.singers))
          }
        })
      },
      // ,
      _normalizeSinger(list) {
        let map = {
          hot: {
            title: HOT_NAME,
            items: []
          }
        }
        list.forEach((item, index) => {
          if(index < HOT_SINGER_LEN) {
            map.hot.items.push(new Singer({
              name: item.Fsinger_name,
              id: item.Fsinger_mid,
            }))
          }
  
          const key = item.Findex
          if(!map[key]) {
            map[key] = {
              title: key,
              items: []
            }
          }
          map[key].items.push(new Singer({
            name: item.Fsinger_name,
            id: item.Fsinger_mid,
          }))
        })
  
        //  map
        let hot = []
        let ret = []
        for (let key in map) {
          let val = map[key]
          if(val.title.match(/[a-zA-Z]/)) {
            ret.push(val)
          }else if(val.title === HOT_NAME) {
            hot.push(val)
          }
        }
  
        //  compareFunction (a-b->)
        ret.sort((a,b) => {
          return a.title.charCodeAt(0) - b.title.charCodeAt(0)
        })
        // Stringindex UTF-16 (065535) NaN
        //  codePointAt()
//        console.log("ABC".charCodeAt(0)) 65
//        console.log("ABC".charCodeAt(1)) 66
        return hot.concat(ret)
      },
      // 1.  2. 
      // mapMutations this.$store.commit('SET_SINGER')
      ...mapMutations({
        setSinger: 'SET_SINGER'
      })
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .singer
    position: fixed
    top: 88px
    bottom: 0
    width: 100%
</style>



