<template>
  <div id="app">
    <m-header></m-header>
    <tab></tab>
    <keep-alive>
      <!---->
      <router-view></router-view>
    </keep-alive>
    <!--player-->
    <player></player>
  </div>
</template>

<script>
  // reslovecomponents
  import MHeader from 'components/m-header/m-header'
  import Tab from 'components/tab/tab'
  import Player from 'components/player/player'
  
  export default {
    name: 'App',
    components: {
      MHeader, Tab, Player
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  // variablemixinindex.stylmain.js
  @import "~common/stylus/variable"
  #app
    color: $color-theme
</style>



